<template>
  <div class="text-center pt-4 container">
    <form @submit="search">
      <input
        v-model="query"
        type="text"
        class="w-50 text-center p-2"
        placeholder="Buscar restaurante"
      />
      <button type="submit" class="btn btn-primary ms-2">Buscar</button>
    </form>

    <div v-for="restaurant in restaurants" :key="restaurant.id">
      <div class="card mx-auto mt-5" style="width: 60%">
        <img :src="restaurant.url_photo" class="card-img-top" />
        <div class="card-body">
          <h5 class="card-title">Restaurante: {{ restaurant.name }}</h5>
          <h6>Ubicación: {{ restaurant.city }} - {{ restaurant.direction }}</h6>
          <p>{{ restaurant.description }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      query: "",
      restaurants: []
    };
  },
  methods: {
    async search(e) {
      e.preventDefault();
      const response = await fetch(
        "https://api-restaurantes-test.herokuapp.com/api/restaurant/search/",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            text: this.query
          })
        }
      );

      const data = await response.json();
      console.log(data);

      if (data.error === null) {
        this.restaurants = data.data;
        return;
      }

      alert(data.error);
    }
  }
};
</script>
